.button {
    color: #fff;
    border: none;
    font-weight: 600;
    cursor: pointer;
}

.disabled {
    border-radius: 3px;
    padding: 5px 24px;
    width: 133px;
    background: #004d99;
    color: white;
    opacity: 0.5;
}

.secondary {
    border: 1px solid #06c;
    border-radius: 3px;
    padding: 5px 24px;
    width: 176px;
    color: #06c;
    background: none;
}

.default {
    border-radius: 3px;
    padding: 5px 24px;
    width: 132px;
    background: #06c;
}

.red {
    border-radius: 3px;
    padding: 5px 24px;
    width: 129px;
    background: #cc1439;
}

.default:hover {
    background: #0080ff;
}

.red:hover {
    background: #ff1a47;
}

.secondary:hover {
    background: #e5f0fa;
}


.default:active {
    background: #06c;
}

.secondary:active {
    background: #c9e5ff;
}

.red:active {
    background: #b21232;
}

.button:disabled {

}
