.sidebar {
    position: fixed;
    top: 0;
    left: -317px; /* Начальная позиция - скрыт за экраном */
    width: 317px;
    height: 100%;
    background-color: #fff;
    transition: left 0.3s ease; /* Анимация для плавного открытия */
    z-index: 1000;
}

.sidebar.open {
    left: 0;
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999;
}

.nav {
    display: flex;
    flex-direction: column;
    padding: 60px 0 0 70px;
}

.nav a {
    color: #000000;
    text-decoration: none;
    font-size: 18px;
    transition: background-color 0.3s ease;
    margin-bottom: 40px;
}

.nav a:hover {
    color: #58acff;
    text-decoration: underline;
}

.nav a.active {
    color: #3d6b99;
    text-decoration: underline;
}
.close {
    position: absolute;
    top: 10px;
    right: 20px;
}

.close img {
    background: #fff;
    border: none;
}
